export default "import React from 'react';\r\nimport {Container, Navbar} from 'react-bootstrap';\r\n\r\nconst Containers = () => {\r\n  return (\r\n    <div>\r\n      <Container>\r\n        <Navbar expand='lg' variant='light' bg='light'>\r\n          <Container>\r\n            <Navbar.Brand href='#'>Navbar</Navbar.Brand>\r\n          </Container>\r\n        </Navbar>\r\n      </Container>\r\n    </div>\r\n  );\r\n};\r\n\r\nexport default Containers;\r\n";